import React, { Component } from "react";
import "./header.css";
import "./Nav.css";

class Nav extends Component {
  state = {};
  render() {
    return (
      <div>
        <div className="container-fluid">
          <div className="row justify-content-between">
            <div className="col-md-2">
              <h1 className="my-logo">JR</h1>
            </div>
            <div className="col-md-4 my-col-nav">
              <ul className="nav navbar-nav d-none d-md-block">
                <b>
                  <a className="text-dark text-decoration-none" href="#">
                    <span className="" /> HOME
                  </a>
                  <a className="text-dark text-decoration-none" href="#">
                    <span className="glyphicon glyphicon-user" /> ABOUT
                  </a>
                  <a className="text-dark text-decoration-none" href="#">
                    <span className="glyphicon glyphicon-user" /> PORTFOLIO
                  </a>
                  <a className="text-dark text-decoration-none" href="#">
                    <span className="glyphicon glyphicon-user" /> CONTACT
                  </a>
                </b>
              </ul>
              <div class="dropdown d-md-none my-col-nav-collapsed">
                <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  Navigation
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                  <a class="dropdown-item" href="#">
                    HOME
                  </a>
                  <a class="dropdown-item" href="#">
                    ABOUT
                  </a>
                  <a class="dropdown-item" href="#">
                    PORTFOLIO
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Nav;
